.tourism-features {
    width: 100%;
    padding-top: 30px;
    text-align: center;
    text-transform: uppercase;
    background-color: $gray;
    font-family: $font;
    
    &__list {
        width: 280px;
        margin: 0 auto;
        padding: 0;
        list-style: none;
    }
    
    &__list-item {
        padding-top: 30px;
        padding-bottom: 30px;
        border-bottom: 1px solid darken($gray, 15%);
        
        &:last-of-type {
            border-bottom: none;
        }
        
        h3 {
            font-family: $font-headings;
        }
    }
}